.flexWrapper{
    margin-top: 7%;
}
.childflexWrapper{
    gap: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cta_btn_wrapp{
    width: 99%;
    height: 4.5rem;
    margin-bottom: 15%;
    cursor: pointer;
}

/* .cta-btn{
    font-size: var(--fs-xs);
} */
.copy{
    font-size: 5rem;
    line-height: 0.9;
}


/*canvas*/
#cvs_wrapper{
    width: 100%;
    aspect-ratio: 1/1 !important;
    object-fit: cover;
}


.icon_wrapper{
    width: 8.5rem;
    height: 4rem;
    overflow: hidden;
    }

.icon{
    width: 4rem ;
    display: inline-block;
    aspect-ratio: 1/1;
    background-color: transparent;
    border: transparent;
}

.in{

    background-image: url(./assets/Capa\ -1.svg);
    background-repeat: no-repeat;
}
.gh{

    background-image: url(./assets/Capa\ 2.svg);
    background-repeat: no-repeat;
}

