.contact_section{
 
    h2, h3, a , p{
        color:var(--flat-grey)
    }
    a { 
        font-size: var(--fs-sm);
        text-decoration: underline;
    }
    a:hover{
        color:var(--accent)
    }
}


.contact_content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}

.contact_items{
    display: flex;
    flex-direction:column;
    justify-content: center;
    
}
.contact_item{
    font-size: clamp(2rem, 5vw + 1rem , 4rem);
}




.footer_copy{
    display: flex;
    justify-content: space-between;
   
}